@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  color: #494949;
}

.page-body {
  background-color: #f4f4f4;
}

.page-body__wrapper {
  max-width: 846px;
  margin: 8rem 10.6rem;
  background-color: #ffffff;
  border-radius: 0 2rem 2rem 2rem;
}

.section-title h3 {
  font-size: 1.8rem;
  line-height: 1.5;
  font-weight: 500;
  color: #0c0c0c;
  margin: 0 0 2rem;
}

.section-padding {
  padding: 4rem 0;
}

.container {
  width: 100%;
  max-width: 846px;
  margin: 0 auto;
  padding: 0 60px;
}

.products {
  overflow: hidden;
}

.products__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.products__item {
  visibility: hidden;
  opacity: 0;
  max-height: 0;
  transform: scale(0.9);
}

.products__item.active {
  visibility: visible;
  opacity: 1;
  max-height: 100%;
  transform: scale(1);
  transition: transform 0.3s linear;
}

.description__wrapper {
  display: flex;
  column-gap: 7rem;
  border-bottom: 1px solid #d9d9d9;
}

.description__image {
  width: 358px;
  height: 232px;
  margin-left: -127px;
  object-fit: cover;
  flex-shrink: 0;
}

.description__image img {
  width: 100%;
  height: 100%;
}

.description h2 {
  font-weight: 700;
  font-size: 2.6rem;
  line-height: 1.5;
  color: #0c0c0c;
  margin: 0 0 2rem;
}

.description p {
  font-size: 1.6rem;
  line-height: 1.5;
  margin: 0 0 1.5rem;
}

.description p:last-child {
  margin: 0;
}

.characteristic__list {
  font-size: 1.6rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 1.6rem;
  column-gap: 1rem;
}

.characteristic__list li {
  margin: 0 0 0 1.8rem;
}

.form__wrapper {
  display: flex;
  position: relative;
}

.form__group {
  width: 100%;
  max-width: 351px;
  margin: 0 2.4rem 0 0;
}

.form__group input {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  height: 100%;
  width: 100%;
  padding: 1.4rem 1.6rem;
  border-radius: 1.6rem;
  border: 1px solid gainsboro;
}

.form__group input:focus {
  border: 1px solid #3922c5;
}

.form__wrapper button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  border: none;
  cursor: pointer;
  color: #ffffff;
  background-color: #3922c5;
  border-radius: 1.6rem;
  padding: 1.4rem 2.4rem;
}

.form__message {
  position: absolute;
  left: 0;
  top: 55px;
  font-size: 1.2rem;
  line-height: 1.3;
  margin-bottom: 0;
  color: red;
}

.confirmation__block {
  box-shadow: 0 10px 40px rgba(12, 12, 12, 0.1);
  border-radius: 2rem;
  padding: 3rem 3.6rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.confirmation__block button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #0c0c0c;
  border-radius: 1.6rem;
  padding: 1.4rem 2.4rem;
  transition: color 0.3s linear, background-color 0.3s ease;
}

.tabs {
  background-color: #f4f4f4;
}

.tabs .container {
  padding: 0;
}

.tabs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.tabs__list button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 2.2rem;
  line-height: 1.5;
  font-weight: 500;
  padding: 2rem 2.4rem;
  border-radius: 2rem 2rem 0 0;
  border: none;
  cursor: pointer;
  position: relative;
  background: #d9d9d9;
  color: #0c0c0c;
}

.tabs__list button.active {
  background-color: #ffffff;
  color: #3922c5;
  cursor: default;
}

@keyframes wave {
  0% {
    transform: translate(-50%, -75%) rotate(0deg);
  }

  100% {
    transform: translate(-50%, -75%) rotate(360deg);
  }
}

@media (min-width: 846px) {
  .confirmation__block button:hover,
  .confirmation__block button:focus {
    color: #ffffff;
    background-color: #3922c5;
    transition: color 0.3s linear, background-color 0.3s ease;
  }

  .btn-liquid {
    position: relative;
    display: block;
    overflow: hidden;
  }

  .btn-liquid__text {
    position: relative;
    z-index: 1;
    pointer-events: none;
  }

  .btn-liquid__wave {
    display: block;
    position: absolute;
    top: -35px;
    left: 0;
    width: 100%;
    height: 200px;
    background-color: #4973ff;
    box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.5);
    transition: 0.5s;
  }

  .btn-liquid__wave::before,
  .btn-liquid__wave::after {
    content: "";
    position: absolute;
    width: 200%;
    height: 130%;
    top: 0;
    left: 50%;
    transform: translate(-50%, -75%);
  }

  .btn-liquid__wave::before {
    border-radius: 45%;
    background-color: rgb(20, 20, 20);
    animation: wave 5s linear infinite;
  }

  .btn-liquid__wave::after {
    border-radius: 40%;
    background-color: rgba(20, 20, 20, 0.5);
    animation: wave 10s linear infinite;
  }

  .btn-liquid__wave:hover,
  .btn-liquid__wave:focus {
    top: -120px;
  }
}

@media (max-width: 767px) {
  .page-body__wrapper {
    max-width: 100%;
    margin: 4rem 2rem;
  }

  .section-title h3 {
    font-size: 1.6rem;
    margin: 0 0 1.5rem;
  }

  .section-padding {
    padding: 3rem 0;
  }

  .container {
    max-width: 100%;
    padding: 0 20px;
  }

  .description__wrapper {
    flex-direction: column;
    row-gap: 2rem;
  }

  .description__image {
    align-self: center;
    width: 295px;
    height: 192px;
    margin-left: 0;
  }

  .description h2 {
    font-size: 2.4rem;
  }

  .description p {
    font-size: 1.4rem;
  }

  .characteristic__list {
    display: flex;
    flex-direction: column;
    row-gap: 1.5rem;
    font-size: 1.4rem;
  }

  .form__wrapper {
    flex-direction: column;
    row-gap: 1.5rem;
  }

  .form__group {
    max-width: none;
    margin: 0;
  }

  .form__message {
    position: static;
  }

  .form__message p {
    margin: 0;
  }

  .tabs__list {
    flex-direction: column;
    row-gap: 20px;
  }

  .tabs__list button {
    width: 100%;
    text-align: left;
    background-color: #ffffff;
    border-radius: 2rem;
    padding: 2rem 5rem 2rem 2.4rem;
  }

  .tabs__list button::after {
    content: "";
    position: absolute;
    background-image: url("../img/decorative/arrow-2.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 24px;
    height: 24px;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.3s linear;
  }

  .tabs__list button.active::after {
    transform: translateY(-50%) rotate(180deg);
  }
}